<template>
    <div>
        <tree-grid
            :columns="columns"
            :rowdata="data"
            :needUpdate="needUpdate"
            :selectedId="deleteIdArr"
            :group="groupid"
            @refreshTable="refreshTable"
            @uploadmodify="uploadmodify"
            @uploaddelete="uploaddelete"
            @updateG="updateG"
            :treeLoading="treeLoading"
            ></tree-grid>
    </div>
</template>

<script>
import treeGrid from './tree-grid-access'
import axios from 'axios';
import qs from 'qs' ;
import urlconfig from '../../config/urlconfig';
axios.defaults.transformRequest = [function (data) {
        return qs.stringify(data)
}];
export default {
        data(){
return {
columns: [
{name: 'ID', prop: 'id', width: 120},
            {name: '删除', delete: true }, //删除、操作不需要width
            {name: 'name字段', prop: 'name', width: 260, isTree: true, edit: true},
//            {name: '操作', operate: true }, //删除、操作不需要width
//            {name: 'level', prop: 'level', width: 120},
//            {name: 'url', prop: 'url', edit: true},
        ],
        data: [],
        needUpdate: Date.now(),
        deleteIdArr:[],
        groupid:0,
        groupinfo:{},
        treeLoading: false,
    }
  },
  created(){
        this.treeLoading = true;
        this.$http.get(urlconfig.getGroupaccess).then(res => {
this.groupid = res.data.group;
        if (res.data.result.length > 0)  {
var a = res.data.result[0].permissions.split(',');
        this.groupinfo = res.data.result[0];
        console.log('now', res.data.result);
        console.log('this.groupinfo', this.groupinfo);
        this.deleteIdArr = a;
                       }
                         this.$http.get(urlconfig.getAccessurl).then(res => {
        this.treeLoading = false;
        this.data = res.data.result;
        this.needUpdate = Date.now();
                            });
                       });
        
  },
  methods: {
        refreshTable(){
this.treeLoading = true;
        this.$http.get(urlconfig.getGroupaccess).then(res => {
this.groupid = res.data.group;
        if (res.data.result.length > 0)  {
var a = res.data.result[0].permissions.split(',');
        this.groupinfo = res.data.result[0];
        console.log('now', res.data.result);
        this.deleteIdArr = a;
                       }
                         this.$http.get(urlconfig.getAccessurl).then(res => {
        this.treeLoading = false;
        this.data = res.data.result;
        this.needUpdate = Date.now();
                            });
                       });
      },
      uploadmodify([data, successFn_callback, faildFn_callback]){
        console.log(data)
        console.log('i will change');
        axios.request({'url':urlconfig.passChange, method:'post', dataType:'json', data:data
         })
                .then(function(res){
                console.log(res.data.result);
                successFn_callback()
                })
                .catch(function(err){
                console.log(err);
                faildFn_callback()
                })
      },
      uploaddelete([deleteIdArray, successFn_callback, faildFn_callback]){
                console.log(deleteIdArray);
                axios.request({'url':urlconfig.passDelete, method:'post', dataType:'json', data:deleteIdArray
         })
                .then(function(res){
                        console.log(res.data.result);
                        successFn_callback()
                })
                .catch(function(err){
                        console.log(err);
                        faildFn_callback()
                })
      },
          updateG([deleteIdArray]){
                        console.log(deleteIdArray);
                        var xx = JSON.stringify(this.groupinfo);
                        var mm = {'del':deleteIdArray.toString(), 'groupinfo':xx};
                            console.log('group---',mm);
                        axios.request({'url':urlconfig.updateG, method:'post', dataType:'json', data:mm
                                        })
                .then(function(res){
                                console.log(res.data.result);
                                        })
                .catch(function(err){
                                console.log(err);
                                        })
      },

  },
  components: {
                                treeGrid
  }
}
</script>




